import React from "react";
import { useTranslation } from "react-i18next";
import "./HealthPage.scss";
import healthBanner from "../../media/resize.jpg";

const HealthPage = () => {
  const { t } = useTranslation();

  return (
    <div className="health-page">
      <div className="banner">
        <img src={healthBanner} alt={t("Salomatlik Xizmatlari")} />
        <div className="overlay">
          <h1>{t("Salomatlik")}</h1>
          <p>{t("Eng yuqori sifatli tibbiy xizmatlar va ilgor texnologiyalar.")}</p>
        </div>
      </div>

      <div className="content">
        <h2>{t("Nega bizni tanlaysiz?")}</h2>
        <p>{t("Bizning sogliqni saqlash sohasidagi xizmatlarimiz bemorlar salomatligini birinchi oringa qoyadi. Har bir bemorga individual yondashuv, zamonaviy texnologiyalar va yuqori malakali shifokorlar bilan xizmat korsatamiz.")}</p>

        <h2>{t("Xizmatlarimiz")}</h2>
        <ul>
          <li> {t("Diagnostika va laboratoriya tahlillari")}</li>
          <li> {t("Stasionar davolanish va ambulator xizmatlar")}</li>
          <li> {t("Mutaxassis maslahatlari")}</li>
          <li> {t("Dorixona va tibbiy mahsulotlar")}</li>
          <li> {t("Tez tibbiy yordam xizmati")}</li>
        </ul>

        <h2>{t("Ishonchli, xavfsiz, professional")}</h2>
        <p>{t("Biz sizga va yaqinlaringizga gamxorlik qilamiz. Barcha xizmatlarimiz xalqaro standartlarga javob beradi.")}</p>
      </div>
    </div>
  );
};

export default HealthPage;
